body {
    font-family: Arial, sans-serif;
    background-color: black;
    padding: 20px;
}

h1 {
    text-align: center;
}

form {
    background-color: black;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    max-width: 400px;
    margin: 0 auto;
}

label,
input,
select,
textarea,
input[type="submit"] {
    display: block;
    margin-bottom: 10px;
}

label {
    font-weight: bold;
}

input,
select,
textarea {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

input[type="submit"] {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
}

input[type="submit"]:hover {
    background-color: #555;
}
